import React from "react";
import { useHistory } from "react-router-dom";
import "./Project.css";

import yarn1 from "../img/yarn1.png";
import yarn2 from "../img/yarn2.png";
import yarn3 from "../img/yarn3.png";
import virus1 from "../img/mockup1.png";
import virus2 from "../img/mockup2.png";
import findfun from "../img/findfun.png";
import Fade from "react-reveal/Fade";

const Project = () => {
  const { push } = useHistory();
  const history = useHistory();

  window.onscroll = function () {
    scrollFunction();
  };
  function linkClick() {
    var li = document.getElementById("link");
    var lis = document.getElementById("links");

    let width = window.innerWidth;
    if (width < 769) {
      var li = document.getElementById("link");
      li.style.width = "27%";
      var lis = document.getElementById("links");
      lis.style.display = "block";
    } else {
      var li = document.getElementById("link");

      if (document.getElementById("link") !== "null") {
        var li = document.getElementById("link");

        console.log(li.style.width);
        if (li.style.width === "90%") {
          li.style.width = "1vw";
          li.style.height = "1vw";
          lis.style.display = "none";
        } else if (li.style.width === "1vw") {
          li.style.width = "90%";
          lis.style.display = "block";
        }
      }
    }
  }

  function scrollFunction() {
    let width = window.innerWidth;
    if (width < 769) {
      var li = document.getElementById("link");
      li.style.width = "27%";
      var lis = document.getElementById("links");
      lis.style.display = "block";
    } else {
      var li = document.getElementById("link");
      var lis = document.getElementById("links");
      if (
        document.body.scrollTop > 50 ||
        document.documentElement.scrollTop > 50
      ) {
        li.style.width = "1vw";
        li.style.height = "1vw";
        lis.style.display = "none";
        li.onclick = linkClick;
      } else {
        li.style.width = "90%";
        lis.style.display = "block";
        li.onclick = linkClick;
      }
    }
  }
  function handleClick() {
    history.push("/");
  }
  return (
    <div className="pro">
      <div id="link">
        <Fade left>
          <div id="links">
            <a onClick={handleClick}>Home</a> | <a href="/">Contact</a> |{" "}
            <a href="./Project">Projects</a> | <a href="#divide2">Education</a>
          </div>
        </Fade>
      </div>
      <h1 className="header1">PROJECTS</h1>
      <div className="cont">
        <h2 className="header2 ">YARN</h2>
        <div id="yarntrio">
          <img src={yarn1}></img>
          <img src={yarn2}></img>
          <img src={yarn3}></img>
        </div>
        <p>
          Yarn was an app created by me and two of my fellow students, in
          collaboration with marketing students in Perth, Australia. The goal
          was to provide the elderly with a way to cope with loneliness during
          the pandemic, by allowing them to connect with others. We accomplished
          this by creating Yarn, an app which allowed the elderly to connect to
          people with the same interests as themselves over a video chat. Yarn
          was designed using the design tool Figma and later implemented through
          Android studios.
        </p>
      </div>
      <div className="cont">
        <h2 className="header2">COMBAT COVID</h2>
        <div id="virustrio">
          <img id="virus1" src={virus1}></img>
          <img id="virus2" src={virus2}></img>
        </div>
        <p>
          Combat Covid on Campus (CCC) was created by me and two other students.
          The app took inspiration from “Popular Times” on Google and tackled
          the question “How to Combat Covid on Campus”. CCC allowed users to
          check-in to campus before going, letting the people who wanted to
          avoid crowds know when campus was busy. The app also allowed for
          potential contract tracing for the COVID-19 virus. CCC was designed in
          Figma and later implemented using React.
        </p>
      </div>
      <div className="cont">
        <h2 className="header2">FINDFUN</h2>
        <div id="findfunimg">
          <img src={findfun}></img>
        </div>
        <p>
          Findfun is a little side project to try out Borded API. The product is
          a webiste where you can set your preferences and then see if they have
          any activities for you. The projects is uploaded on GitHub, you can
          find it <a href="https://github.com/Ljungholm-a/findfun"> here</a>
        </p>

        <p></p>
      </div>
    </div>
  );
};
export default Project;
